}

.hc-button--secondary:active {
  color: var(--hc-text-on-color);
  background-color: var(--hc-button-secondary-active);
  border: 2px solid var(--hc-button-secondary-border-active);
}

/* focus for secondary -- commented out for now */
.hc-button--secondary:disabled {
