/* ====================================== */
/* new styling based on govuk */
/* https://github.com/alphagov/govuk-frontend/blob/9f05cea739d14d4cbf18e79b17228045ca84b3ba/packages/govuk-frontend/src/govuk/components/checkboxes/_index.scss */
/* ====================================== */
/* accessibly styling radio buttons/checkboxes */
/* https://www.sarasoueidan.com/blog/inclusively-hiding-and-styling-checkboxes-and-radio-buttons/ */
/* wrapper for input and label */
/* includes min-height for consistent sizing of form elements */
/* margin-bottom adds space between items (this might be changed to gap) */
/* position: relative so that absolute positioning can be used for label and input */
.hc-checkboxes {
  margin-top: var(--hc-spacing-05);
}

.hc-checkbox__item {
  display: block;
  position: relative;
  min-height: 44px;
  padding-left: 44px; /* This should equal width of the checkbox size*/
  margin-bottom: var(--hc-spacing-05);
  clear: left;
}

/* removes margin bottom from last element */
.hc-checkbox__item:last-child,
.hc-checkbox__item:last-of-type {
  margin-bottom: 0;
}

.hc-checkbox__input {
  position: absolute;
  top: 1px;
  left: 1px;
  z-index: 1;
  width: 44px;
  height: 44px;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.hc-checkbox__label {
  font-size: var(--hc-font-fieldLabel-size-regular);
  color: var(--hc-text-primary, #171a1d);
  font-weight: 400;
  display: inline-block;
  margin-bottom: 0px;
  padding-left: var(--hc-spacing-04);
  padding-right: var(--hc-spacing-04);
  padding-bottom: var(--hc-spacing-01);
  cursor: pointer;
  touch-action: manipulation;
  max-width: 80ch;
}

/* [ ] Check box styling */
/* Should update to 44px but will need to redo the checkbox and label padddins */
.hc-checkbox__label::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 44px;
  height: 44px;
  border: var(--hc-border-width-medium) solid var(--hc-border-color-strong);
  background: var(--hc-transparent);
  border-radius: 4px;
  cursor: pointer;
}

/* The check mark is a box with a border on the left and bottom side (),
 rotated 45 degrees */
.hc-checkbox__label::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 10px;
  left: 10px;
  width: 24px;
  height: 14px;
  color: white;
  transform: rotate(-45deg);
  border: solid;
  border-width: 0 0 4px 4px;
  /* Fix bug in IE11 caused by transform rotate (-45deg). */
  /* See: alphagov/govuk_elements/issues/518 */
  border-top-color: transparent;
  opacity: 0;
  background: transparent;
  cursor: pointer;
}

.hc-checkbox__hint {
  padding-left: var(--hc-spacing-03);
}

/* Focus state */
.hc-checkbox__input:focus + .hc-checkbox__label::before {
  /* border: var(--hc-border-style-focus); */
  outline: var(--hc-outline-style-focus);
  outline-offset: 3px;
  border-radius: 2px;
}

/* Makes checkbox visible */
.hc-checkbox__input:checked + .hc-checkbox__label::after {
  opacity: 1;
  transition: 0.1ms;
}

/* checked state styling */
.hc-checkbox__input:checked + .hc-checkbox__label::before {
  background-color: var(--hc-background-brand); /*adds blue backround in checked state*/
  border: var(--hc-border-brand);
}

/* Glues the input field and button toether */
.hc-search-wrapper {
  display: flex;
  flex-direction: row;
  gap: var(--hc-spacing-00);
}

/* Stylin for font typed into field */
input.hc-input[type=search] {
  font-family: var(--hc-font-family);
